*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root{
  background: linear-gradient(112.1deg, rgb(32, 38, 57) 11.4%, rgb(63, 76, 119) 70.2%);
}

.pokemon-card:hover{
background: #131211 !important;
border: 2px solid orange !important; 
transition: 0.2s;
}

.link-name{
  text-decoration: none;
  color: white;
  font-size: 1rem;
}

.link-name:hover{
  cursor: pointer;
  color: #e3e3e3;
}

.read-more{
  text-decoration: none;
  color: white;
  font-size: 1.2rem;
}



.read-card:hover{
  background: white !important;

  transition: 1s;
}

.read-more:hover {
  color: #183373 !important;
}


