/* Search Bar Styles */
.search-container {
    max-width: 500px;
    margin: 0 auto;
    padding: 0 15px;
}

.search-input {
    padding: 12px 16px;
    border: 2px solid #e1e5e9;
    border-radius: 8px;
    font-size: 16px;
    transition: all 0.3s ease;
}

.search-input:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.search-info {
    text-align: center;
}

/* No Results Styling */
.no-results {
    padding: 40px 20px;
    color: #6c757d;
}

.no-results h5 {
    margin-bottom: 10px;
    color: #495057;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .search-container {
        max-width: 100%;
        padding: 0 10px;
    }
    
    .search-input {
        font-size: 14px;
        padding: 10px 14px;
    }
}